const inputMediaDevices = () => navigator.mediaDevices.enumerateDevices()
  // eslint-disable-next-line no-undef
  .then((devices) => devices.filter((device) => device instanceof InputDeviceInfo));

const supportedConstraints = () => navigator.mediaDevices.getSupportedConstraints();

const canGetUserStream = () => !!navigator.mediaDevices && !!navigator.mediaDevices.getUserMedia;

const getUserStream = (constraints) => navigator.mediaDevices.getUserMedia(constraints);

const canGetDisplayStream = () => !!navigator.mediaDevices && !!navigator.mediaDevices.getDisplayMedia;

const getDisplayStream = (constraints) => navigator.mediaDevices.getDisplayMedia(constraints);

const canRequestApprovalDialog = () => canGetUserStream();

// eslint-disable-next-line max-len
const requestApprovalDialog = ({ audio, video } = { audio: true, video: true }) => getUserStream({ audio: !!audio, video: !!video })
  .then((stream) => {
    if (stream) {
      stream.getTracks().forEach((track) => track.stop());
      return true;
    }
    return false;
  }).catch(() => false);

export {
  inputMediaDevices,
  supportedConstraints,
  canGetUserStream,
  getUserStream,
  canGetDisplayStream,
  getDisplayStream,
  canRequestApprovalDialog,
  requestApprovalDialog,
};

